___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}
body {
    margin: 0 auto;
}

.wrapper {
    max-width: 1980px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.main {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* .registration-form {
    padding: 20px;
} */

.registration-fieldset {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    width: 300px;
}
.registration-title {
    text-align: center;
    font-size: 20px;
}

.USLabel {
    padding: 0 0 2px 5px;
}
.USInput__input {
    width: 100%;
    padding: 4px 8px;
    border: 2px solid #d9d9d9;
    color: rgba(0, 0, 0, 0.88);
    border-radius: 2px;
    box-sizing: border-box;
    transition: all 0.2s;
}
.USInput__input:focus-visible {
    outline: none;
    border-color: #000;
}
.USInput__input:hover {
    outline: none;
    border-color: #000;
    transition: all 0.2s;
}
.USInput:last-of-type {
    margin-bottom: 10px;
}

.USInput__container__select {
    width: 100%;
    padding: 4px 6px;
    border-radius: 2px;
    border: 2px solid #d9d9d9;
    transition: all 0.2s;
}
.USInput__container__select:hover {
    border-color: #000;
    transition: all 0.2s;
}
.USInput__container__select:focus-visible {
    outline: none;
    border-color: #000;
}
.USInput__hint {
    display: none;
    font-size: 12px;
    color: rgb(204 70 70);
    padding: 0 4px;
}

.registration__btn {
    padding: 4px 15px;
    border: 0px #fff;
    height: 32px;
    border-radius: 6px;
    font-size: 14px;
    cursor: pointer;
    background-color: #1677ff;;
    color: #fff;
    transition: all 0.2s;
}

.registration__btn:hover {
    background-color: #4096ff;
    transition: all 0.2s;
}

